body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 40px;
  background: #f8bbd0;

;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  color: #424242;
  margin-bottom: 10px;
}

.square {
  color: #ce93d8;
  background: #ffeeff;
  border: 2px solid #c48b9f;
  float: left;
  font-size: 96px;
  font-weight: bold;
  line-height: 136px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 8rem;
  height: 8rem;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.container {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.game {
  display: flex;
  flex-direction: column;
}

.game-board{
  margin:auto;
} 
.game-info {
  font-size: 48px;
  color: #424242;
  margin:auto;
  margin-top: 20px;
  text-align: center;
}
.game-info>div {
  width: 22rem;
}
.game-info ol {
  font-size: 30px;
  margin-top: 5px;
}
.game-info button,
.restart-button {
  margin-top: 20px;
  text-align: center;
}
.game-info button,
.restart:hover {
  box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
}
.game-info button,
.restart:focus {
  outline: none;
}
.game-info button,
.restart:active {
  box-shadow: 0 1px #424242;
  transform: translateY(1px);
}
.game-info button,
.restart {
  -webkit-transition-duration: 0.4s; /* Safari */
  transition-duration: 0.4s;
  background-color: #424242;
  color: #fffde7;
  border: none;
  font-size: 20px;
  line-height: 48px;
  height: 48px;
  width: 12rem;
  text-align: center;
  border-radius: 4px;
}